				"excerpt": "<p></p>\n<pre><code class=\"lang-js\">let data = [<span class=\"hljs-number\">250</span>, <span class=\"hljs-number\">210</span>, <span class=\"hljs-number\">170</span>, <span class=\"hljs-number\">100</span>, <span class=\"hljs-number\">190</span>]\n\nlet rectWidth = <span class=\"hljs-number\">25</span>\n\nsvg.selectAll(<span class=\"hljs-string\">'rect'</span>)\n .data(data)\n .enter()\n .append(<span class=\"hljs-string\">'rect'</span>)\n .attr(<span class=\"hljs-string\">'y'</span>, <span class=\"hljs-function\"><span class=\"hljs-params\">(d, i)</span> =&gt;</span> height - d)\n .attr(<span class=\"hljs-string\">'x'</span>, <span class=\"hljs-function\"><span class=\"hljs-params\">(d, i)</span> =&gt;</span> i * rectWidth)\n .attr(<span class=\"hljs-string\">'height'</span>, d =&gt; d)\n .attr(<span class=\"hljs-string\">'width'</span>, rectWidth - <span class=\"hljs-number\">2</span>)\n .attr(<span class=\"hljs-string\">'fill'</span>, <span class=\"hljs-string\">'steelblue'</span>)\n</code></pre>\n<p></p>\n<p></p>\n<blockquote>\n<p>Scales are a convenient abstraction for a fundamental task in visualization: mapping a dimension of abstract data to a visual representation.</p>\n</blockquote>\n<p><strong> - Scale - </strong></p>\n<p><code>d3-scale</code> </p>\n<ul>\n<li>Continuous Scales</li>\n<li>Ordinal Scales</li>\n</ul>\n"
