				"excerpt": "<h2>\n            <a name=\"Cookie-amp-Session\" role=\"anchor\">\n              <span>Cookie &amp; Session</span>\n            </a>\n          </h2><p>HTTP  SessionSession  Cookie</p>\n<p> Cookie </p>\n<h3>\n            <a name=\"Cookie\" role=\"anchor\">\n              <span>Cookie</span>\n            </a>\n          </h3><p>Cookie  HTTP </p>\n<ul>\n<li> cookie <ul>\n<li> HTTP  set-cookie </li>\n<li> cookie  name = value </li>\n</ul>\n</li>\n<li> cookie </li>\n<li> cookie </li>\n</ul>\n<p> Cookie  Cookie </p>\n<p> Node.js  Cookie  package</p>\n<h3>\n            <a name=\"Session\" role=\"anchor\">\n              <span>Session</span>\n            </a>\n          </h3><p>Cookie  Session</p>\n<p></p>\n<ul>\n<li>Cookie </li>\n<li>Session </li>\n</ul>\n<p>Cookie  Session  <code>SESSION_ID</code>  express  <code>connect.sid</code>  SESSION_ID  Session SESSION_ID  Session  Cookie </p>\n<p> Session </p>\n<ul>\n<li>RAM</li>\n<li>Database</li>\n<li>Cache (e.g. Redis)</li>\n</ul>\n<p>Session </p>\n<h2>\n            <a name=\"AJAX-\" role=\"anchor\">\n              <span>AJAX </span>\n            </a>\n          </h2><p></p>\n<h3>\n            <a name=\"\" role=\"anchor\">\n              <span></span>\n            </a>\n          </h3><p> Chrome </p>\n<p></p>\n<ul>\n<li></li>\n<li>GET / POST </li>\n<li></li>\n<li></li>\n</ul>\n<p></p>\n<h3>\n            <a name=\"\" role=\"anchor\">\n              <span></span>\n            </a>\n          </h3><p>token / sid / secret</p>\n<p></p>\n<ul>\n<li></li>\n<li></li>\n</ul>\n<p> HTML  Cookie Header </p>\n<p> JS </p>\n<h2>\n            <a name=\"\" role=\"anchor\">\n              <span></span>\n            </a>\n          </h2><p> HTTP  GET / POST </p>\n<p></p>\n<h2>\n            <a name=\"\" role=\"anchor\">\n              <span></span>\n            </a>\n          </h2><p>Nodejs  Chrome  Firefox  Package E2E Testing </p>\n<p> Night Watch JS</p>\n<pre><code><span class=\"hljs-built_in\">module</span>.exports = {\n <span class=\"hljs-string\">'Demo test Google'</span> : <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-params\">(client)</span></span> {\n client\n .url(<span class=\"hljs-string\">'http://www.google.com'</span>)\n .waitForElementVisible(<span class=\"hljs-string\">'body'</span>, <span class=\"hljs-number\">1000</span>)\n .<span class=\"hljs-built_in\">assert</span>.title(<span class=\"hljs-string\">'Google'</span>)\n .<span class=\"hljs-built_in\">assert</span>.visible(<span class=\"hljs-string\">'input[type=text]'</span>)\n .setValue(<span class=\"hljs-string\">'input[type=text]'</span>, <span class=\"hljs-string\">'rembrandt van rijn'</span>)\n .waitForElementVisible(<span class=\"hljs-string\">'button[name=btnG]'</span>, <span class=\"hljs-number\">1000</span>)\n .click(<span class=\"hljs-string\">'button[name=btnG]'</span>)\n .pause(<span class=\"hljs-number\">1000</span>)\n .<span class=\"hljs-built_in\">assert</span>.containsText(<span class=\"hljs-string\">'ol#rso li:first-child'</span>,\n <span class=\"hljs-string\">'Rembrandt - Wikipedia'</span>)\n .<span class=\"hljs-keyword\">end</span>();\n }\n};\n</code></pre><p>Cookie / Session / </p>\n<p>&nbsp;</p>\n"
